.page-header {
  position: fixed;
  z-index: 4;
  width: 100%;
  box-shadow: 4px 4px 8px 0 rgba(34, 60, 80, 0.09);
  background-color: $color-default-white;
  padding-top: 30px;
  padding-bottom: 30px;

  &--static {
    position: static;
  }

  @media (max-width: $desktop-width) {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  @media (max-width: $tablet-width) {
    padding-top: 5px;
    padding-bottom: 5px;
  }
}

.page-header__logo {
  display: inline-block;
  margin-right: 40px;

  @media (max-width: $desktop-width) {
    margin-right: 25px;
  }

  @media (max-width: $tablet-width) {
    margin-top: 9px;
    margin-right: 14px;
  }

  &:hover {
    opacity: 1;
  }
}

.page-header__logo svg {
  fill: $color-main-teal;

  @media (max-width: $tablet-width) {
    width: 77px;
    height: 77px;
  }
}

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
}

.page-header__nav {
  display: flex;
  align-items: center;
  margin-right: 30px;

  @media (max-width: $tablet-width) {
    margin-right: 0;
  }
}

.page-header__list {
  @include list-style;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  min-width: 500px;

  @media (max-width: $desktop-width) {
    min-width: 100%;
  }

  @media (max-width: $tablet-width) {
    align-items: start;
    flex-direction: column;
  }
}

.page-header__item {
  @media (max-width: $desktop-width) {
    width: auto;
  }

  @media (max-width: $tablet-width) {
    width: 100%;
  }
}

.page-header__list-contacts {
  @include list-style;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  max-height: 75px;
  align-content: end;

  @media (max-width: $desktop-width) {
    min-width: auto;
  }

  @media (max-width: $tablet-width) {
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    min-width: fit-content;
    align-self: flex-start;
  }
}

.page-header__list-contacts li {
  margin-left: 20px;

  @media (max-width: $desktop-width) {
    margin-left: 10px;
  }
}

.page-header__list a,
.page-header__list-contacts a {
  color: $color-main-font;
  font-weight: 700;
  font-size: 20px;

  @media (max-width: $desktop-width) {
    font-size: 14px;
  }

  @media (max-width: $tablet-width) {
    font-size: 11px;
    line-height: 14px;
  }

  &:hover {
    opacity: 1;
    border-bottom: 2px solid $color-accent-orange;
    transition: 0.4s;
  }
}

.page-header__list-contacts a {
  color: $color-main-teal;
}

.page-header__list a {
  margin-left: 20px;

  @media (max-width: $desktop-width) {
    margin-left: 15px;
  }

  @media (max-width: $tablet-width) {
    margin-left: 8px;
  }
}
